
export function GetRequest(url,data){
	var baseUrl = uni.getStorageSync('OtherUrl')
	var promise = new Promise((resolve,reject)=>{
		uni.request({
			url:baseUrl +url,
			data:data,
			method:'GET',
			timeout:5000,
			success: (res) => {
				resolve(res);
			},
			fail: (e) => {
				reject(e)
			}
		})
	});
	return promise;
}

