export function PostRequest(url,data){
	var promise = new Promise((resolve,reject)=>{
		uni.request({
			url:url,
			data:data,
			method:"POST",
			timeout:5000,
			success: (res) => {
				console.log(res)
				resolve(res);
			},
			fail: (e) => {
				reject(e)
			}
		})
	});
	return promise
}